.header {
  width: 100%;
  min-height: 658px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: conic-gradient(from 150.87deg at 16.03% 39.06%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
}

.header__container {
  max-width: 1366px;
  width: 100%;
  min-height: 104px;
  padding: 30px 103px;
  position: relative;
}

.header__title {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.header__info {
  width: 298px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: absolute;
  top: 172px;
  left: 201px;
}

.header__info h1 {
  font-family: $ff-alumni;
  font-weight: 800;
  font-size: 92px;
  line-height: 111px;
  letter-spacing: 0.05em;
  color: $color-default-white;
  margin: 0;
  margin-bottom: 47px;
  text-align: right;
  text-transform: uppercase;
}

.header__info span {
  font-family: $ff-alumni;
  font-weight: 600;
  font-size: 40px;
  line-height: 40px;
  letter-spacing: 0.03em;
  color: $color-default-white;
  margin: 0;
  text-transform: uppercase;
  margin: 0 56px 22px 0;
}

.header__title span {
  font-family: $ff-alumni;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: $color-default-white;
  margin: 0;
}

.header__info ul {
  font-family: $ff-roboto;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: $color-default-white;
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 185px;
  left: -79px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.header__info a {
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  display: flex;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  padding: 20px 52px;
  border: 4px solid $color-default-white;
  width: 234px;
  background-color: transparent;
  user-select: none;
}

.header__info a:hover,
.header__info a:focus {
  background-color: $color-sapphire;
  outline: none;
}

.header__info a:active {
  background-color: $color-egyptian-blue;
}

.header__container picture {
  position: absolute;
  top: 51px;
  left: 563px;
}
