.promotion {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $color-default-white;
}

.promotion__container {
  max-width: 1366px;
  width: 100%;
  padding: 100px 103px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.promotion h2 {
  font-family: $ff-alumni;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  color: $color-sapphire;
  margin: 0;
}

.promotion__wrapper {
  display: grid;
  grid-template-columns: 560px 560px;
  grid-template-rows: 211px 211px;
  gap: 40px;
}

.promotion__1 {
  width: 560px;
  height: 462px;
  display: flex;
  grid-column: 1/2;
  grid-row: 1/3;
}

.promotion__1-info {
  width: 280px;
  background-color: $color-torch-red;
  padding: 45px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.promotion__1-info span {
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 28px;
  line-height: 44px;
  color: $color-default-white;
}

.promotion__1-info b {
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 70px;
  line-height: 109px;
  color: $color-default-white;
}

.promotion__1-info p {
  font-family: $ff-roboto;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  color: $color-default-white;
  margin: 0;
}

.promotion__1-info a {
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  border-bottom: 2px solid $color-default-white;
}

.promotion__2 {
  grid-column: 2/3;
  grid-row: 1/2;
}

.promotion__3 {
  grid-column: 2/3;
  grid-row: 2/3;
}

.promotion__2,
.promotion__3 {
  width: 560px;
  height: 211px;
  border: 7px solid #D10A42;
    border-image: linear-gradient(148.82deg, #D10A42 0.85%, #00359F 100%);
    border-image-slice: 1;
  padding: 35px;
}

.promotion__2 h4,
.promotion__3 h4 {
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 26px;
  line-height: 18px;
  color: $color-resolution-blue;
  margin: 0;
}

.promotion__2 p,
.promotion__3 p {
  font-family: $ff-roboto;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: $color-resolution-blue;
  margin: 0;
}

.promotion__2 a,
.promotion__3 a {
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-resolution-blue;
  border-bottom: 2px solid $color-resolution-blue;
  margin: 0;
}
