.contacts-form {
  width: 100%;
  min-height: 337px;
  background-color: $color-sapphire;
  padding: 70px 103px;
}

.contacts-form h2 {
  font-family: $ff-alumni;
  font-weight: 800;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  color: $color-default-white;
  margin: 0;
}

.contacts-form__inputs {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.contacts-form__inputs button {
  grid-column: 1/3;
  background-color: transparent;
  border: 3px solid $color-default-white;
  padding: 21px 166px;
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  cursor: pointer;
  user-select: none;
}

.contacts-form__inputs button:hover,
.contacts-form__inputs button:focus {
  background-color: $color-torch-red;
  outline: none;
}

.contacts-form__inputs button:active {
  background-color: $color-egyptian-blue;
}

.contacts-form__inputs input {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #74819C;
}

.contacts-form__inputs input::placeholder {
  font-family: $ff-roboto;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: $color-default-white;
}
